<template>
    <div class="h5Upload">
        <div class="tit">
            <span>上传图片</span>
            <span><span class="len">1</span>/3</span>
        </div>
        <div class="pic-con">
            <div class="pic-item">
                <el-upload action="#" >
                    <img src="../images/phone/my-pic.png" alt="">
                </el-upload>
            </div>
            <div class="pic-item">
                <el-upload action="#" >
                    <img src="../images/phone/idcard-z.png" alt="">
                </el-upload>
            </div>
            <div class="pic-item">
                <el-upload action="#" >
                    <img src="../images/phone/idcard-f.png" alt="">
                </el-upload>
            </div>
        </div>
        <div class="btn-con">
            <button>保存</button>
        </div>
    </div>
</template>
<script>
export default {
  name: "h5Upload",
  data() {
    return {
		
    };
  },
  methods: {
	 
  }
};
</script>
<style lang="less">
.h5Upload {
    width: 100%;
    min-height: 100%;
    padding: 0 15px;
    background: #E4EDF6;
    .tit {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.75rem;
        padding: 10px;
        color: #AEBECE;
        .len {
            color: #444444;
        }
    }
    .pic-con {
        background: #fff;
        border-radius: 5px;
        padding: 1.8rem;
        .pic-item {
            position: relative;
            padding-bottom: 63.5%;
            width: 100%;
            background: url('../images/certification/upload-bg.png') no-repeat;
            background-size: 100% 100%;
            padding: 10px 16px;
            margin-bottom: 30px;
            .el-upload  {
                width: 100%;
                border: none;
                img {
                    width: 100%;
                    height: 100%;
                }
            }

        }

    }
    .btn-con {
        text-align: center;
        padding: 30px 0;
        button {
            width: 100%;
            height: 2.375rem;
            border-radius: 1000px;
            background: #3B6AF1;
            color: #fff;
            font-size: 0.9rem;
            cursor: pointer;
        }
    }

}

</style>


