<script>
import bottom from "@/components/bottom";
export default {
  name: "certification",
  components: {
	bottom
  },
  data() {
    return {
        active: 1,

        // 立即开户
        tel: '',
        imgCode: '',
        telCode: '',
        obj: {
            imgCodeUrl: '',
        },
        time: 0, // 验证码倒计时
        timeSetInterVal: '', // 验证码定时器
        isSend: false, //是否发送了验证码

        userName: '',
        radio:1,
        checkList: ['选中且禁用','复选框 A'],

        digitalShow: false, //数字申请书弹框

    };
  },
  mounted() {
      if(this.timeSetInterVal) {
        clearInterval(this.timeSetInterVal)
      }
      this.getImgCode()
  },
  destroyed() {
     if(this.timeSetInterVal) {
        clearInterval(this.timeSetInterVal)
      }
  },
  methods: {
      // 步骤1 立即开户
      step1Btn() {
        var reg = /^1[3456789]\d{9}$/
        if(!reg.test(this.tel)) {
            this.$message.error('请输入正确的手机号!')
            return
        }
        if(!this.imgCode) {
            this.$message.error('请输入图形验证码!')
            return
        }
         if(!this.telCode) {
            this.$message.error('请输入手机验证码!')
            return
        }
      },
      // 获取图形验证码
      getImgCode() {
          let that = this;
          that.$api.verify().then(res=>{
            //   console.log(res)
                // if(res.data.code == 0){
                    
                // }
            })
            // setTimeout(() =>{
            //     that.imgCodeUrl = 'http://coopera.xfd365.com/user/auth/verify'
            // })
      },
      // 获取验证码
      getTelCode() {
        let that = this;
        let reg = /^1[3456789]\d{9}$/
        if(!reg.test(that.tel)) {
            that.$message.error('请输入正确的手机号')
            return
        }
        if(!that.imgCode) {
            that.$message.error('请输入图形验证码!')
            return
        }
        if(that.time) return
        console.log(1)
        that.$api.get_code({
            phone: that.tel,
            code: that.imgCode
        }).then(res=>{
            that.$message.success(res.data.msg)
            if(res.data.code == 0){
                that.time = 60
                that.isSend = true
                that.timeSetInterVal = setInterval(() =>{
                    that.time--
                    if(that.time <= 0) {
                        clearInterval(that.timeSetInterVal)
                    }
                },1000)
            }
        })
        
      }
  }
};
</script>
