                margin-right: 1rem;
            }
            .inquire-desc-con {
                display: flex;
                flex-direction: column;
                .tit {
                    font-size: 0.875rem;
                    font-weight: bold;
                    color: #3B6AF1;
                    padding-bottom: 0.5rem;
                }
