import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import certification from '@/pages/certification'
import h5Upload from '@/pages/h5Upload'
import confirmInfo from '@/pages/confirmInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: index
    },
    {
      path: '/certification',
      name: '认证',
      component: certification
    },
    {
      path: '/h5Upload',
      name: 'h5上传',
      component: h5Upload
    },
    {
      path: '/confirmInfo',
      name: '确认信息',
      component: confirmInfo
    }
  ]
})


